<template>
  <div class="add-task mb-4 d-flex flex-column align-items-center">
    <h2 class="p-3 bg-light rounded">Añadir nueva tarea</h2>
    <form @submit.prevent="addTask" style="width: 100%; max-width: 500px;">
      <div class="mb-3 bg-light p-3 rounded">
        <label for="taskName" class="form-label">Nombre de la Tarea</label>
        <input type="text" class="form-control" id="taskName" v-model="newTask.name" required>
      </div>

      <div class="mb-3 bg-light p-3 rounded">
        <label for="taskDetails" class="form-label">Detalles de la Tarea</label>
        <textarea class="form-control" id="taskDetails" rows="3" v-model="newTask.details"></textarea>
      </div>

      <div class="mb-3 bg-light p-3 rounded">
        <label for="dueDate" class="form-label">Fecha de vencimiento</label>
        <input type="date" class="form-control" id="dueDate" v-model="newTask.dueDate">
      </div>

      <div class="mb-3 bg-light p-3 rounded">
        <label for="category" class="form-label">Categoría</label>
        <input type="text" class="form-control" id="category" v-model="newTask.category">
      </div>

      <div class="mb-3 bg-light p-3 rounded">
        <label for="priority" class="form-label">Prioridad</label>
        <select class="form-control" id="priority" v-model="newTask.priority">
          <option value="Alta">Alta</option>
          <option value="Media">Media</option>
          <option value="Baja">Baja</option>
        </select>
      </div>

      <button type="submit" class="btn btn-primary">{{ taskToEdit ? 'Actualizar Tarea' : 'Añadir Tarea' }}</button>
    </form>
  </div>
</template>
<script>
export default {
  props: ['taskToEdit'],
  data() {
    return {
      newTask: {
        name: '',
        details: '',
        dueDate: '',
        category: '',
        priority: 'Media',
        completed: false,
      },
    };
  },
  methods: {
    addTask() {
      if (this.taskToEdit) {
        this.newTask.id = this.taskToEdit.id;
      }
      this.$emit('add-task', { ...this.newTask });

      this.newTask = {
        name: '',
        details: '',
        dueDate: '',
        category: '',
      };
    },
  },
  watch: {
    taskToEdit(newVal) {
      if (newVal) {
        this.newTask = { ...newVal };
      } else {
        this.newTask = {
          name: '',
          details: '',
          dueDate: '',
          category: '',
        };
      }
    },
  },
};
</script>
