<template>
  <div class="task-list mb-4 d-flex flex-column align-items-center">
    <h2 class="mb-4">Lista de Tareas</h2>
    <div class="task p-3 mb-3 rounded shadow-sm text-white" v-for="task in tasks" :key="task.id" :class="getTaskClass(task)">
  <h3 class="task-name font-weight-bold mb-2" :class="{ 'completed-task': task.completed }">{{ task.name }}</h3>
  <p class="task-details mb-2" :class="{ 'completed-task': task.completed }">{{ task.details }}</p>
      <div class="task-meta mb-3">
        <span class="mr-2"><strong>Categoría:</strong> {{ task.category }}</span>
        <span class="mr-2"><strong>Fecha de vencimiento:</strong> {{ task.dueDate }}</span>
        <span><strong>Prioridad:</strong> {{ task.priority }}</span>
      </div>
      <div class="text-center">
        <button class="btn btn-light mr-2 mb-2" @click="editTask(task.id)">Editar</button>
        <button class="btn btn-danger mr-2 mb-2" @click="deleteTask(task.id)">Borrar</button>
        <input type="checkbox" v-model="task.completed" @change="emitUpdateTask(task)"> 
        <label class="form-check-label mb-2" for="defaultCheck1">
          Completado
        </label>
      </div>
    </div>
  </div>
</template>




<script>
export default {
  props: ['tasks'],
  methods: {
    editTask(taskId) {
      this.$emit('edit-task', taskId);
    },
    deleteTask(taskId) {
      this.$emit('delete-task', taskId);
    },
    emitUpdateTask(task) {
    this.$emit('update-task', task);
  },
  getTaskClass(task) {
      if (task.priority === 'Alta') {
        return 'bg-light';
      } else if (task.priority === 'Media') {
        return 'bg-success text-white';
      } else {
        return 'bg-primary text-white';
      }
    },
  },
};
</script>

<style scoped>
.completed-task {
  text-decoration: line-through;
}
.bg-light {
  background-color: #db1414 !important;
}
.bg-success {
  background-color: #e8591c !important;
}
.bg-primary {
  background-color: #8de0a6 !important;
}
.btn, input[type="checkbox"] {
  margin: 0 5px;
}
.mb-4 {
    margin-top: 30px;
    color: aliceblue;
}
</style>