<template>
  <div id="app" class="d-flex flex-column align-items-center">
    <add-task @add-task="handleAddTask" :task-to-edit="taskToEdit" class="w-100 mb-3" style="max-width: 500px;"></add-task>
     <b-dropdown variant="secondary" class="mb-3">
      <template #button-content>
        Ordenar por prioridad
      </template>
      <b-dropdown-item class="my-dropdown-item" @click="sortTasks('Alta')">Alta</b-dropdown-item>
      <b-dropdown-item class="my-dropdown-item" @click="sortTasks('Media')">Media</b-dropdown-item>
      <b-dropdown-item class="my-dropdown-item" @click="sortTasks('Baja')">Baja</b-dropdown-item>
    </b-dropdown>
    <task-list :tasks="tasks" @edit-task="handleEditTask" @delete-task="handleDeleteTask" @update-task="handleUpdateTask" class="w-100" style="max-width: 500px;"></task-list>
  </div>
</template>


<script>
import AddTask from './components/AddTask.vue'
import TaskList from './components/TaskList.vue'

export default {
  components: {
    AddTask,
    TaskList
  },
  data() {
    return {
      tasks: [],
      taskToEdit: null,
    };
  },
  methods: {
    handleAddTask(task) {
      task.id = this.tasks.length + 1;
      this.tasks.push(task);
      this.saveTasks();
    },
    handleUpdateTask(task) {
      const index = this.tasks.findIndex(t => t.id === task.id);
      if (index !== -1) {
        this.tasks[index] = task;
        this.saveTasks();
      }
    },
    handleDeleteTask(taskId) {
      this.tasks = this.tasks.filter(t => t.id !== taskId);
      this.saveTasks();
    },
    saveTasks() {
      localStorage.setItem('tasks', JSON.stringify(this.tasks));
    },
    handleEditTask(taskId) {
      this.taskToEdit = this.tasks.find(task => task.id === taskId);
    },
    sortTasks(priority) {
  this.tasks.sort((a, b) => {
    const priorityLevels = {
      'Alta': 3,
      'Media': 2,
      'Baja': 1,
    };
    if (priority === a.priority && priority === b.priority) {
      return priorityLevels[b.priority] - priorityLevels[a.priority];
    } else if (priority === a.priority) {
      return -1;
    } else if (priority === b.priority) {
      return 1;
    } else {
      return 0;
    }
  });
},
  },
  watch: {
    taskToEdit(newVal) {
      if (!newVal) {
        this.taskToEdit = null;
      }
    },
  },
  created() {
    const tasks = localStorage.getItem('tasks');
    if (tasks) {
      this.tasks = JSON.parse(tasks);
    }
  },
};
</script>
